import { useEffect, useContext, useState } from "react";
import Layout from 'layouts'
import './Dashboard.scss'
import toast from "react-hot-toast";

// import { useMediaQuery } from '@uidotdev/usehooks'
import { useChainId, useAccount, useBalance, useReadContract, useWriteContract } from "wagmi";
import { useConnectModal } from "@rainbow-me/rainbowkit";
import { zeroAddress } from "viem";
import { ethers } from "ethers";

import 'react-tabs/style/react-tabs.scss'
import {
  TEST_NET,
  HASH_ADDRESS_TEST,
  HASH_ADDRESS_MAIN,
  MINER_ADDRESS_TEST,
  MINER_ADDRESS_MAIN,
} from "../config";
import HashAbi from "../config/abis/HASH_ABI.json";
import MinerAbi from "../config/abis/MINER_ABI.json";
import { RefreshContext } from '../context/RefreshContext'

import RightPanel from "components/RigthPanel";

// Add animation keyframes style
const nftAnimationStyle = document.createElement('style');
nftAnimationStyle.innerHTML = `
  @keyframes pulse {
    0% { transform: scale(1); opacity: 1; }
    100% { transform: scale(1.05); opacity: 0.9; }
  }
  
  @keyframes float {
    0% { transform: translateY(0px); }
    50% { transform: translateY(-10px); }
    100% { transform: translateY(0px); }
  }
  
  @keyframes glow {
    0% { opacity: 0.5; }
    100% { opacity: 0.8; }
  }
`;
document.head.appendChild(nftAnimationStyle);

const useRefresh = () => {
  const { fast, slow } = useContext(RefreshContext)
  return { fastRefresh: fast, slowRefresh: slow }
}

function formatNumber(num) {
  if (num >= 1e9) return (num / 1e9).toFixed(1) + 'B';
  if (num >= 1e6) return (num / 1e6).toFixed(1) + 'M';
  if (num >= 1e3) return (num / 1e3).toFixed(1) + 'K';
  return num.toString();
}

const dailyBlocks = 86400 / 1.1

export default function BettingPage() {
  // const navigate = useNavigate(); //  call useNavigate inside the component

  // const isMobile = useMediaQuery("only screen and (max-width: 768px)");
  const { fastRefresh } = useRefresh();

  const { openConnectModal } = useConnectModal();
  const { writeContractAsync } = useWriteContract();

  const chainId = useChainId();
  const { isConnected, address } = useAccount();
  const { data: balance, refetch: refetchBalance } = useBalance({ address: address });

  ////////HASH//////////////////////////////////////
  const { data: tokenBalance, refetch: refetchTokenBalance } = useReadContract({
    address: chainId === TEST_NET ? HASH_ADDRESS_TEST : HASH_ADDRESS_MAIN,
    abi: HashAbi,
    functionName: "balanceOf",
    args: [isConnected ? address : zeroAddress],
    chainId: chainId,
  });

  /////// Miner //////////////////////////////////
  const { data: hashRate, refetch: refetchPlayerHashrate } = useReadContract({
    address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
    abi: MinerAbi,
    functionName: "playerHashrate",
    args: [isConnected ? address : zeroAddress],
    chainId: chainId,
  });

  const { data: ownerToFacility, refetch: refetchOwnerToFacility } = useReadContract({
    address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
    abi: MinerAbi,
    functionName: "ownerToFacility",
    args: [isConnected ? address : zeroAddress],
    chainId: chainId,
  });

  const { data: blocksUntilNextHalving, refetch: refetchBlocksUntilNextHalving } = useReadContract({
    address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
    abi: MinerAbi,
    functionName: "blocksUntilNextHalving",
    args: [],
    chainId: chainId,
  });

  const { data: pendingRewards, refetch: refetchPendingRewards } = useReadContract({
    address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
    abi: MinerAbi,
    functionName: "pendingRewards",
    args: [isConnected ? address : zeroAddress],
    chainId: chainId,
  });

  const { data: playerHashcoinPerBlock, refetch: refetchPlayerHashcoinPerBlock } = useReadContract({
    address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
    abi: MinerAbi,
    functionName: "playerHashcoinPerBlock",
    args: [isConnected ? address : zeroAddress],
    chainId: chainId,
  });

  const [isClaiming, setIsClaiming] = useState(false);

  useEffect(() => {
    refetchBalance();
    refetchTokenBalance();
    refetchPlayerHashrate();
    refetchOwnerToFacility();
    refetchBlocksUntilNextHalving();
    refetchPendingRewards();
    refetchPlayerHashcoinPerBlock();
  }, [fastRefresh, isConnected, address, chainId]);

  const handleClaim = async () => {
    if (!isConnected || !address) {
      toast.error("Please connect your wallet first.");
      return;
    }
    if (!pendingRewards || parseFloat(ethers.formatEther(pendingRewards)) <= 0) {
      toast.error("No rewards to claim.");
      return;
    }

    try {
      setIsClaiming(true);

      await writeContractAsync({
        address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
        abi: MinerAbi,
        functionName: "claimRewards",
        args: [],
      })
        .then(() => {
          setIsClaiming(false);
          toast.success("Rewards claimed successfully!");
        })
        .finally(() => {
          setIsClaiming(false);
        })
        .catch((err) => {
          setIsClaiming(false);
        });
    } catch (error) {
      console.error("Error claiming rewards:", error);
      toast.error("Failed to claim rewards. Please try again.");
    }
  }

  return (
    <Layout>
      {
        isConnected ?
          <>
            <div className="lg:grid px-3 lg:px-0 grid-cols-1 mx-auto relative lg:top-[50px] justify-center md:grid-cols-2 gap-6 max-w-7xl w-full">
              {/* Left Panel */}
     <div className="bg-gradient-to-br from-black/90 to-gray-900/90 backdrop-blur-xl w-full rounded-2xl p-6 space-y-8 border border-yellow-500/20 shadow-2xl">
      <div className="w-full space-y-8">
        {/* Resources Section */}
        <div className="space-y-4">
          <div className="flex items-center space-x-2 mb-4">
            <div className="w-2 h-2 bg-gradient-to-r from-yellow-500 to-orange-400 rounded-full"></div>
            <h2 className="text-transparent bg-gradient-to-r from-yellow-400 to-orange-400 bg-clip-text font-bold text-xl lg:text-2xl">
              Resources
            </h2>
          </div>

          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4">
            {/* ETH Balance */}
            <div className="relative group">
              <div className="absolute inset-0 bg-gradient-to-r from-yellow-500/20 to-orange-400/20 rounded-xl blur-sm group-hover:blur-md transition-all duration-300"></div>
              <div className="relative bg-gray-800/80 backdrop-blur-sm border-2 border-yellow-500/50 rounded-xl p-4 text-center hover:border-yellow-400 transition-all duration-300">
                <div className="flex items-center justify-center mb-2">
                  <svg className="w-5 h-5 text-yellow-400 mr-2" fill="currentColor" viewBox="0 0 24 24">
                    <path d="M12,1.75L5.75,6.25V11.5C5.75,16.25 8.5,20.5 12,21.75C15.5,20.5 18.25,16.25 18.25,11.5V6.25L12,1.75M12,7C13.04,7 13.88,7.84 13.88,8.88C13.88,9.92 13.04,10.75 12,10.75C10.96,10.75 10.13,9.92 10.13,8.88C10.13,7.84 10.96,7 12,7Z" />
                  </svg>
                  <span className="text-gray-300 text-sm font-medium">ETH Balance</span>
                </div>
                <div className="text-white font-bold text-lg">
                  {balance !== undefined ? Number.parseFloat(ethers.formatEther(balance.value)).toFixed(3) : "0.000"}
                </div>
                <div className="text-yellow-400 text-xs font-semibold">ETH</div>
              </div>
            </div>

            {/* Spaces Left */}
            <div className="relative group">
              <div className="absolute inset-0 bg-gradient-to-r from-blue-500/20 to-purple-400/20 rounded-xl blur-sm group-hover:blur-md transition-all duration-300"></div>
              <div className="relative bg-gray-800/80 backdrop-blur-sm border-2 border-blue-500/50 rounded-xl p-4 text-center hover:border-blue-400 transition-all duration-300">
                <div className="flex items-center justify-center mb-2">
                  <svg className="w-5 h-5 text-blue-400 mr-2" fill="currentColor" viewBox="0 0 24 24">
                    <path d="M12,2A10,10 0 0,0 2,12A10,10 0 0,0 12,22A10,10 0 0,0 22,12A10,10 0 0,0 12,2M12,4A8,8 0 0,1 20,12A8,8 0 0,1 12,20A8,8 0 0,1 4,12A8,8 0 0,1 12,4M12,6A6,6 0 0,0 6,12A6,6 0 0,0 12,18A6,6 0 0,0 18,12A6,6 0 0,0 12,6Z" />
                  </svg>
                  <span className="text-gray-300 text-sm font-medium">Available</span>
                </div>
                <div className="text-white font-bold text-lg">
                  {ownerToFacility ? Number(ownerToFacility[1] - ownerToFacility[2]) : 0}
                </div>
                <div className="text-blue-400 text-xs font-semibold">SPACES</div>
              </div>
            </div>

            {/* HASH Balance */}
            <div className="relative group sm:col-span-2 lg:col-span-1">
              <div className="absolute inset-0 bg-gradient-to-r from-green-500/20 to-emerald-400/20 rounded-xl blur-sm group-hover:blur-md transition-all duration-300"></div>
              <div className="relative bg-gray-800/80 backdrop-blur-sm border-2 border-green-500/50 rounded-xl p-4 text-center hover:border-green-400 transition-all duration-300">
                <div className="flex items-center justify-center mb-2">
                  <svg className="w-5 h-5 text-green-400 mr-2" fill="currentColor" viewBox="0 0 24 24">
                    <path d="M5,9V21H1V9H5M9,21A2,2 0 0,1 7,19V9C7,8.45 7.22,7.95 7.59,7.59L14.17,1L15.23,2.06C15.5,2.33 15.67,2.7 15.67,3.11L15.64,3.43L14.69,8H21C22.11,8 23,8.9 23,10V12C23,12.26 22.95,12.5 22.86,12.73L19.84,19.78C19.54,20.5 18.83,21 18,21H9M9,19H18.03L21,12V10H12.21L13.34,4.68L9,9.03V19Z" />
                  </svg>
                  <span className="text-gray-300 text-sm font-medium">HASH Tokens</span>
                </div>
                <div className="text-white font-bold text-lg">
                  {tokenBalance ? Number.parseFloat(ethers.formatEther(tokenBalance)).toFixed(2) : "0.00"}
                </div>
                <div className="text-green-400 text-xs font-semibold">HASH</div>
              </div>
            </div>
          </div>
        </div>

        {/* Statistics Section */}
        <div className="space-y-4">
          <div className="flex items-center space-x-2 mb-4">
            <div className="w-2 h-2 bg-gradient-to-r from-purple-500 to-pink-400 rounded-full"></div>
            <h2 className="text-transparent bg-gradient-to-r from-purple-400 to-pink-400 bg-clip-text font-bold text-xl lg:text-2xl">
              Statistics
            </h2>
          </div>

          <div className="bg-gray-800/50 backdrop-blur-sm rounded-xl p-6 border border-gray-700/50 space-y-4">
            <div className="flex justify-between items-center py-3 border-b border-yellow-500/30">
              <span className="text-gray-300 font-medium text-lg">You are mining</span>
              <span className="text-yellow-400 font-bold text-lg">
                {playerHashcoinPerBlock
                  ? formatNumber(Number.parseFloat(ethers.formatEther(playerHashcoinPerBlock)) * dailyBlocks)
                  : "--"}{" "}
                HASH/DAY
              </span>
            </div>

            <div className="flex justify-between items-center py-3 border-b border-yellow-500/30">
              <span className="text-gray-300 font-medium text-lg">Your hash rate is</span>
              <span className="text-yellow-400 font-bold text-lg">{hashRate ? Number(hashRate) : "--"} GH/S</span>
            </div>

            <div className="flex justify-between items-center py-3">
              <span className="text-gray-300 font-medium text-lg">Blocks until halving</span>
              <span className="text-yellow-400 font-bold text-lg">
                {blocksUntilNextHalving ? blocksUntilNextHalving : "--"} BLOCKS
              </span>
            </div>
          </div>
        </div>

        {/* Claim Section */}
        <div className="relative">
          <div className="absolute inset-0 bg-gradient-to-r from-yellow-500/10 to-orange-400/10 rounded-2xl blur-xl"></div>
          <div className="relative bg-gradient-to-br from-gray-800/90 to-gray-900/90 backdrop-blur-sm rounded-2xl p-6 border border-yellow-500/30 text-center space-y-4">
            <div className="flex items-center justify-center mb-4">
              <svg className="w-8 h-8 text-yellow-400 mr-3" fill="currentColor" viewBox="0 0 24 24">
                <path d="M7,15H9C9,16.08 10.37,17 12,17C13.63,17 15,16.08 15,15C15,13.9 13.96,13.5 11.76,12.97C9.64,12.44 7,11.78 7,9C7,7.21 8.47,5.69 10.5,5.18V3H13.5V5.18C15.53,5.69 17,7.21 17,9H15C15,7.92 13.63,7 12,7C10.37,7 9,7.92 9,9C9,10.1 10.04,10.5 12.24,11.03C14.36,11.56 17,12.22 17,15C17,16.79 15.53,18.31 13.5,18.82V21H10.5V18.82C8.47,18.31 7,16.79 7,15Z" />
              </svg>
              <h3 className="text-white font-bold text-xl lg:text-2xl">Pending Rewards</h3>
            </div>

            <div className="bg-black/30 rounded-xl p-4 mb-4">
              <p className="text-gray-300 text-sm mb-2">You Have Mined</p>
              <p className="text-transparent bg-gradient-to-r from-yellow-400 to-orange-400 bg-clip-text font-bold text-2xl lg:text-3xl">
                {pendingRewards ? Number.parseFloat(ethers.formatEther(pendingRewards)).toFixed(2) : "0.00"} $HASH
              </p>
            </div>

            <button
              className="relative bg-gradient-to-r from-yellow-500 to-orange-400 hover:from-yellow-400 hover:to-orange-300 disabled:from-gray-600 disabled:to-gray-700 font-bold w-full py-4 text-amber-900 disabled:text-gray-400 text-lg px-6 rounded-xl transition-all duration-300 transform hover:scale-105 hover:shadow-lg hover:shadow-yellow-500/25 active:scale-95 disabled:hover:scale-100 disabled:cursor-not-allowed"
              onClick={handleClaim}
              disabled={isClaiming}
            >
              <span className="relative z-10 flex items-center justify-center">
                {isClaiming && (
                  <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-amber-900" fill="none" viewBox="0 0 24 24">
                    <circle
                      className="opacity-25"
                      cx="12"
                      cy="12"
                      r="10"
                      stroke="currentColor"
                      strokeWidth="4"
                    ></circle>
                    <path
                      className="opacity-75"
                      fill="currentColor"
                      d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
                    ></path>
                  </svg>
                )}
                {isClaiming ? "Claiming..." : "Claim Now"}
              </span>
              {!isClaiming && (
                <div className="absolute inset-0 bg-white/20 rounded-xl opacity-0 hover:opacity-100 transition-opacity duration-300"></div>
              )}
            </button>
          </div>
        </div>
      </div>
    </div>

              <RightPanel />
            </div>
            <div className="relative mt-[92px] mb-[40px]">
              <div className="flex justify-center flex-wrap gap-3">
                {/* Twitter */}
                <a
                  href="https://twitter.com/hashlordz"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="flex  items-center gap-3 no-underline bg-[#0000009e] w-[200px] hover:bg-[#2a2c2f] rounded-lg px-4 py-2 transition duration-200"
                >
                  <svg width="33" height="33" className='block' viewBox="0 0 33 33" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M32.492 16.2449C32.492 7.27199 25.218 -0.00201416 16.245 -0.00201416C7.27208 -0.00201416 -0.00195312 7.27199 -0.00195312 16.2449C-0.00195312 25.2179 7.27208 32.4919 16.245 32.4919C25.218 32.4919 32.492 25.2179 32.492 16.2449Z" fill="#0EA5E9" fill-opacity="0.3" />
                    <path d="M23.059 11.777C22.572 12.021 22.084 12.102 21.516 12.183C22.084 11.858 22.49 11.371 22.653 10.721C22.166 11.046 21.597 11.208 20.947 11.371C20.46 10.884 19.728 10.559 18.997 10.559C17.291 10.559 15.992 12.183 16.398 13.808C14.205 13.727 12.255 12.671 10.874 11.046C10.143 12.265 10.549 13.808 11.686 14.62C11.28 14.62 10.874 14.458 10.468 14.295C10.468 15.514 11.361 16.651 12.58 16.976C12.174 17.057 11.767 17.139 11.361 17.057C11.686 18.113 12.661 18.926 13.88 18.926C12.905 19.657 11.443 20.063 10.062 19.901C11.28 20.632 12.661 21.119 14.123 21.119C19.079 21.119 21.841 16.976 21.678 13.158C22.247 12.833 22.734 12.346 23.059 11.777Z" fill="#73D5FF" />
                  </svg>
                  <div className="text-white">
                    <p className="text-sm mb-0 font-semibold">Twitter</p>
                    <p className="text-xs mb-0 text-gray-400">@hashlordz</p>
                  </div>
                </a>

                {/* Discord */}
                <a
                  href="https://discord.gg/hashlordz"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="flex  items-center gap-3 no-underline bg-[#0000009e] w-[200px] hover:bg-[#2a2c2f] rounded-lg px-4 py-3 transition duration-200"
                >
                  <svg width="33" height="33" viewBox="0 0 33 33" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M32.492 16.2449C32.492 7.27199 25.218 -0.00201416 16.245 -0.00201416C7.27208 -0.00201416 -0.00195312 7.27199 -0.00195312 16.2449C-0.00195312 25.2179 7.27208 32.4919 16.245 32.4919C25.218 32.4919 32.492 25.2179 32.492 16.2449Z" fill="#0EA5E9" fill-opacity="0.3" />
                    <path d="M23.059 11.777C22.572 12.021 22.084 12.102 21.516 12.183C22.084 11.858 22.49 11.371 22.653 10.721C22.166 11.046 21.597 11.208 20.947 11.371C20.46 10.884 19.728 10.559 18.997 10.559C17.291 10.559 15.992 12.183 16.398 13.808C14.205 13.727 12.255 12.671 10.874 11.046C10.143 12.265 10.549 13.808 11.686 14.62C11.28 14.62 10.874 14.458 10.468 14.295C10.468 15.514 11.361 16.651 12.58 16.976C12.174 17.057 11.767 17.139 11.361 17.057C11.686 18.113 12.661 18.926 13.88 18.926C12.905 19.657 11.443 20.063 10.062 19.901C11.28 20.632 12.661 21.119 14.123 21.119C19.079 21.119 21.841 16.976 21.678 13.158C22.247 12.833 22.734 12.346 23.059 11.777Z" fill="#73D5FF" />
                  </svg>
                  <div className="text-white">
                    <p className="text-sm mb-0 font-semibold">Discord</p>
                    <p className="text-xs mb-0 text-gray-400">discord.gg/hashlordz</p>
                  </div>
                </a>

                {/* Telegram */}
                <a
                  href="https://t.me/hashlordz"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="flex  items-center gap-3 no-underline bg-[#0000009e] w-[200px] hover:bg-[#2a2c2f] rounded-lg px-4 py-2 transition duration-200"
                >
                  <svg width="34" height="33" viewBox="0 0 34 33" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M33.1689 16.2449C33.1689 7.27199 25.8949 -0.00201416 16.9219 -0.00201416C7.94899 -0.00201416 0.674988 7.27199 0.674988 16.2449C0.674988 25.2179 7.94899 32.4919 16.9219 32.4919C25.8949 32.4919 33.1689 25.2179 33.1689 16.2449Z" fill="#3B82F6" fill-opacity="0.3" />
                    <path d="M22.187 15.839V19.901C22.186 20.439 21.972 20.955 21.591 21.335C21.211 21.716 20.695 21.93 20.157 21.932H17.237C17.129 21.932 17.026 21.889 16.95 21.813C16.874 21.736 16.831 21.633 16.831 21.525C16.831 21.418 16.874 21.314 16.95 21.238C17.026 21.162 17.129 21.119 17.237 21.119H20.157C20.479 21.118 20.789 20.989 21.017 20.761C21.245 20.533 21.374 20.223 21.375 19.901V19.83C21.245 19.877 21.107 19.901 20.969 19.901H20.157C19.833 19.901 19.523 19.772 19.295 19.544C19.066 19.315 18.938 19.005 18.938 18.682V16.651C18.938 16.328 19.066 16.018 19.295 15.79C19.523 15.561 19.833 15.433 20.157 15.433H21.355C21.253 14.317 20.736 13.28 19.905 12.528C19.075 11.775 17.992 11.363 16.872 11.371H16.867C15.745 11.36 14.661 11.772 13.831 12.525C13 13.278 12.483 14.316 12.383 15.433H13.582C13.904 15.434 14.214 15.563 14.442 15.791C14.67 16.019 14.799 16.329 14.8 16.651V18.682C14.799 19.005 14.67 19.314 14.442 19.542C14.214 19.771 13.904 19.899 13.582 19.901H12.769C12.447 19.899 12.137 19.771 11.909 19.542C11.681 19.314 11.552 19.005 11.551 18.682V15.839C11.551 15.143 11.689 14.453 11.956 13.81C12.224 13.167 12.616 12.583 13.11 12.093C13.604 11.602 14.19 11.214 14.835 10.95C15.48 10.687 16.17 10.554 16.867 10.559H16.907C18.307 10.56 19.649 11.117 20.639 12.107C21.629 13.097 22.186 14.439 22.187 15.839Z" fill="#3B82F6" />
                  </svg>
                  <div className="text-white">
                    <p className="text-sm mb-0 font-semibold">Telegram</p>
                    <p className="text-xs mb-0 text-gray-400">t.me/hashlordz</p>
                  </div>
                </a>
              </div>
            </div>
          </>
          :
          <>
             <div className="flex items-center justify-center mt-[100px] p-4">
      <div className="relative bg-black/80 backdrop-blur-xl rounded-2xl p-8 text-center max-w-md w-full border border-white/10 shadow-2xl">
        {/* Decorative gradient border */}
        <div className="absolute inset-0 rounded-2xl bg-gradient-to-r from-yellow-500/20 via-orange-400/20 to-yellow-500/20 blur-sm -z-10"></div>

        {/* Wallet icon */}
        <div className="mx-auto w-16 h-16 bg-gradient-to-r from-yellow-500 to-orange-400 rounded-full flex items-center justify-center mb-6">
          <svg className="w-8 h-8 text-amber-900" fill="currentColor" viewBox="0 0 24 24">
            <path d="M21 18v1c0 1.1-.9 2-2 2H5c-1.11 0-2-.9-2-2V5c0-1.1.89-2 2-2h14c1.1 0 2 .9 2 2v1h-9c-1.11 0-2 .9-2 2v8c0 1.1.89 2 2 2h9zm-9-2h10V8H12v8zm4-2.5c-.83 0-1.5-.67-1.5-1.5s.67-1.5 1.5-1.5 1.5.67 1.5 1.5-.67 1.5-1.5 1.5z" />
          </svg>
        </div>

        <h2 className="text-white font-bold text-3xl mb-3 bg-gradient-to-r from-white to-gray-300 bg-clip-text text-transparent">
          Connect Your Wallet
        </h2>

        <p className="text-gray-400 mb-8 text-lg leading-relaxed">
          Securely connect your wallet to start your mining journey and unlock exclusive features.
        </p>

        <button
          onClick={openConnectModal}
          className="relative bg-gradient-to-r from-yellow-500 to-orange-400 hover:from-yellow-400 hover:to-orange-300 font-bold w-full py-4 text-amber-900 text-lg px-6 rounded-xl transition-all duration-300 transform hover:scale-105 hover:shadow-lg hover:shadow-yellow-500/25 active:scale-95"
        >
          <span className="relative z-10">Connect Wallet</span>
          <div className="absolute inset-0 bg-white/20 rounded-xl opacity-0 hover:opacity-100 transition-opacity duration-300"></div>
        </button>

        {/* Security note */}
        <div className="mt-6 flex items-center justify-center text-gray-500 text-sm">
          <svg className="w-4 h-4 mr-2" fill="currentColor" viewBox="0 0 24 24">
            <path d="M12,1L3,5V11C3,16.55 6.84,21.74 12,23C17.16,21.74 21,16.55 21,11V5L12,1M12,7C13.4,7 14.8,8.6 14.8,10V11H16V18H8V11H9.2V10C9.2,8.6 10.6,7 12,7M12,8.2C11.2,8.2 10.4,8.7 10.4,10V11H13.6V10C13.6,8.7 12.8,8.2 12,8.2Z" />
          </svg>
          Your connection is secure and encrypted
        </div>
      </div>
    </div>
          </>
      }

    </Layout>
  )
}
